:root{
  --color-white: #ffffff;
  --color-black: #000000;
  --color-red-100: #FFD7DC;
  --color-red-200: #FFA3A7;
  --color-red-300: #FF7C74;
  --color-red-400: #FF6347;
  --color-red-500: #BC4731;
  --color-red-600: #792C1E;
  --color-red-700: #36130C;

  --color-blue-100: #D7FFFD;
  --color-blue-200: #A3FFFF;
  --color-blue-300: #74FBFF;
  --color-blue-400: #47E3FF;
  --color-blue-500: #31A9BC;
  --color-blue-600: #1E6E79;
  --color-blue-700: #0C3236;

  --text-bold: bold;

  --text-sm: 0.875rem;
  --text-md: 1rem;
  --text-lg: 1.5rem;
  --text-xlg: 2rem;

   /* Spacing */
  /* 1rem = 16px */
  --space-0: 0;
  --space-1: 0.25rem;
  --space-2: 0.5rem;
  --space-3: 0.75rem;
  --space-4: 1rem;
  --space-5: 1.25rem;
  --space-6: 1.5rem;
  --space-8: 2rem;
  --space-10: 2.5rem;
  --space-12: 3rem;
  --space-16: 4rem;
  --space-20: 5rem;
  --space-24: 6rem;


}
*{
  border:0;
  padding:0;
}

body {
  margin: 0;
  font-family: Sansation;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: var(--color-blue-100);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{
  
    z-index: -100;
    height: 900px;
    width: 1024px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 600px;
    background-color: var(--color-red-400);

}
.title{
  margin-bottom: 130px;
  font-size: var(--text-xlg);
  font-weight: bold;
}
.session{
  margin-bottom: 32px;
  font-size: var(--text-xlg);
  font-weight: bold;
}

button{
  cursor: pointer;
  outline: none;
}
button.arrow{
  background-color: var(--color-red-400);  
}

#timer-wrapper{
  display: flex;
  flex-direction: row;
}
#session-label{
  margin-right: 180px;
  margin-bottom: var(--space-8);
}
#wrapper{
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.timer-title{
  font-size: var(--text-lg);  
  margin-bottom: var(--space-1);
}
.count{
  font-size: var(--text-lg);
}
.arrow{
  margin: 0 var(--space-2);
}

#time-left{
  margin-bottom: var(--space-2);
  font-size: 144px;
  font-weight: bold;
}

#button-wrapper{
  margin-bottom: 280px;
}

#button-wrapper button {
  padding: var(--space-1) var(--space-4);
  margin: 0 var(--space-1);
  font-size: var(--text-xlg);
  font-weight: bold;
  background-color: var(--color-blue-200);

  border-radius: 6px;
}
